.App {
  text-align: center;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
  background: #5133a4;
  color: white
}

.spacer {
  height: 70px;
}

.jumbotron {
  background-image: url("./yosemite.jpg")
}